<template>
    <div class="main">
      <div id="imgsdiv">
        <img referrer="no-referrer|origin|unsafe-url" :src="urlParam" >
      </div>
      <div v-if="isButtonVisible" id="bbutton" class="bbutton" @click="scrollToTop">
        ↑
      </div>
      <div class="loadmore">
        <button class="btn" @click="getmore(10)">加载更多</button>
        <button class="btn" @click="getmore(100)">加载100</button>
        <button class="btn" @click="jump(10)">跳跃10</button>
        <button class="btn" @click="jump(50)">跳跃50</button>
        <button class="btn" @click="jump(100)">跳跃100</button>
      </div>
    </div>
</template>

<script>
import jQuery from 'jquery'
export default {
  name: 'Detail',
  data () {
    return {
      isButtonVisible: false,
      urlParam: null
    }
  },
  created () {
    this.urlParam = this.$route.params.url // 从$route.params获取url参数
    console.log(this.urlParam)
  },
  mounted () {
    window.addEventListener('scroll', this.checkTop)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this.checkTop)
  },
  methods: {
    getmore (x) {
      if (x <= 10) {
        x = 10
      }
      // 找到最后一张图
      var lastimg = jQuery('img').last().attr('src')
      // 取图 名
      var imgname = lastimg.substring(lastimg.lastIndexOf('/') + 1)

      // 提取名字中的数字 123
      var reg1 = /\d+/g
      var imgnum = imgname.match(reg1)
      var num = imgnum.pop()
      for (var i = 1; i <= x; i++) {
        var newnum = num - 0 + i
        var name = String(newnum)
        if (num.length > String(newnum).length) {
          // 长度不一样 前面需要加0
          while (num.length > name.length) {
            name = '0' + name
          }
        }
        var reg = new RegExp('(.*)' + num)
        var n1 = imgname.replace(reg, '$1' + name)
        var n = n1.replace(' ', '%20')

        var newn = lastimg.replace(imgname, n)
        jQuery('#imgsdiv img').last().after('<img referrer="no-referrer|origin|unsafe-url" src="' + newn + '" >')
      }
    },
    jump (x) {
      // 找到最后一张图
      var lastimg = jQuery('img').last().attr('src')
      // 取图 名
      var imgname = lastimg.substring(lastimg.lastIndexOf('/') + 1)
      // 提取名字中的数字 123
      var reg1 = /\d+/g
      var imgnum = imgname.match(reg1)
      var num = imgnum.pop()
      // 最后一个数字

      var newnum = num - 0 + x
      var name = String(newnum)
      if (num.length > String(newnum).length) {
        // 长度不一样 前面需要加0
        while (num.length > name.length) {
          name = '0' + name
        }
      }
      var reg = new RegExp('(.*)' + num)
      var n1 = imgname.replace(reg, '$1' + name)
      var n = n1.replace(' ', '%20')
      var newn = lastimg.replace(imgname, n)
      jQuery('#imgsdiv img').last().after('<img referrer="no-referrer|origin|unsafe-url" src="' + newn + '" alt="">')
    },
    checkTop () {
      const scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      const windowHeight = document.documentElement.clientHeight
      const scrollHeight = document.documentElement.scrollHeight
      this.isButtonVisible = scrollTop > 0
      // 距离底部距离加大
      if (scrollTop + windowHeight + 5 >= scrollHeight) {
        this.getmore(10)
      }
    },
    scrollToTop () {
      window.scrollTo({ top: 0, behavior: 'smooth' })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
a {
  color: #42b983;
}
img{
  width: 100%;
}
</style>



